@import url(https://fonts.googleapis.com/css?family=Montserrat:regular,bold,italic&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Quicksand:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  font-family: 'Quicksand', Arial, Helvetica, sans-serif;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#logo-wrapper {
  display: flex;
  flex: 1 1;
}

input[name=artistId] {
  min-width: 180px;
}

header {
  display: flex;
  align-items: center;
  padding: 20px 40px;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

.nav-link {
  padding: 12px 20px;
  border: 1px solid transparent;
  font-size: 12px;
  line-height: 1;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
}

.nav-link:last-child {
  border: 1px solid white;
}

.nav-link:hover {
  opacity: 0.6;
}

#spotify-logo {
  margin-top: 30px;
  width: 90px;
}

#lhm-logo {
  height: 40px;
}

@media only screen and (max-width: 600px) {
  header {
    padding: 0;
  }
}

